import Vue from 'vue'
import VueRouter from 'vue-router'
import Makanan from '../src/components/HelloWorld.vue'
import Minuman from '../src/components/HelloKedua.vue'

Vue.use(VueRouter)

export default new VueRouter({
    routes: [
        {
            path: '/makanan',
            name: 'makanan',
            component: Makanan
        },
        {
            path: '/minuman',
            name: 'minuman',
            component: Minuman
        },
    ]
})